import { Component, OnInit } from '@angular/core';
import * as THREE from 'three';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls';
@Component({
  selector: 'app-canvas-webgl',
  templateUrl: './canvas-webgl.component.html',
  styleUrls: ['./canvas-webgl.component.css']
})
export class CanvasWebglComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

  const scene = new THREE.Scene();
  const sizes = {width:500,height:500}
  const camera = new THREE.PerspectiveCamera(45, sizes.width/sizes.height, 0.1, 100);
  camera.position.z = 15
  scene.add(camera)
  
  const canvas = document.querySelector('.webgl')
  const renderer = new THREE.WebGLRenderer({ canvas:canvas,alpha:true })
  renderer.setSize( sizes.width, sizes.height );
  renderer.setPixelRatio(2)

  const controls = new OrbitControls(camera,canvas)
  controls.enableDamping = true  
  controls.enablePan = false
  controls.enableZoom = false
  controls.autoRotate = true
  controls.autoRotateSpeed = 5
  const SphereGeometry = new THREE.SphereGeometry(3,64,64);
  const SphereMaterial = new THREE.MeshStandardMaterial({
    color:0x7b2cbf,
    metalness: 0.1, // Set the metalness property
  roughness: 0.5, // Set the roughness property (0 for smooth, 1 for rough)
  })

  const MeshSphere = new THREE.Mesh(SphereGeometry,SphereMaterial)
  scene.add(MeshSphere)

  const pointLight = new THREE.SpotLight( 0xffffff,300, 500);
  pointLight.position.set( 0, 10, 10 );
  scene.add( pointLight );

  window.addEventListener("load",()=>{
    if(window.innerWidth < 550){
      sizes.width = 380
      sizes.height = 380
      renderer.setSize( sizes.width, sizes.height );
    }
    else{
      sizes.width = 500
      sizes.height = 500
      renderer.setSize( sizes.width, sizes.height );
    }
  })


  const animate = () => {
    // Add any animations or interactions here
    controls.update()
    renderer.render(scene, camera);
    window.requestAnimationFrame(animate);

  };
  
  animate();
   
  }

}
